.btn-primary {
  color: #fff;
  background: linear-gradient(to right, #418DE8 0%, #725AFF 50%, #418DE8 100%);
  background-size: 200% auto;
  border-width: 0px;
  transition: all 0.2s ease-in-out;
}

.btn-primary:hover {
  color: #fff;
  background: linear-gradient(to right, #418DE8 0%, #725AFF 50%, #418DE8 100%);
  background-size: 200% auto;
  background-position: right center;
}

.btn-outline-primary {
  background: linear-gradient(180deg, #725AFF 0%, #418DE8 100%);
  background-clip: text;
  border: solid 1px transparent;
  color: transparent;
  position: relative;
  border-radius: 20px;
  /* border color gradient */
}

.btn-outline-primary::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  border-radius: inherit;
  margin: -1px;
  border: solid 1px transparent;
  background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0)), linear-gradient(180deg, #725AFF 0%, #418DE8 100%);
  background-origin: border-box;
  background-clip: content-box, border-box;
  box-shadow: 2px 1000px 1px #fff inset;
  transition: box-shadow 0.2s ease-in-out;
}

.btn-outline-primary:hover {
  color: #fff;
  border-color: transparent;
  background-color: none;
}

.btn-outline-primary:hover::before {
  box-shadow: none;
}

.btn-secondary, .btn-secondary:hover {
  background: linear-gradient(180deg, #725AFF 0%, #418DE8 100%);
  background-clip: text;
  border: solid 1px transparent;
  color: transparent;
  position: relative;
  box-shadow: 0px 3px 20px rgba(114, 90, 255, 0.5);
}

.btn-secondary::before, .btn-secondary:hover::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: inherit;
  background: #fff;
  z-index: -1;
}

.btn-white {
  border: solid 1px transparent;
  color: transparent;
  background: #fff;
  box-shadow: 0px 3px 20px rgba(114, 90, 255, 0.5);
  transition: transform 200ms;
}

.btn-white:hover {
  transform: scale(1.1);
}

a.underline {
  display: inline-block;
  line-height: 2;
}

a.underline::after {
  content: " ";
  background: linear-gradient(to right, #418DE8 0%, #725AFF 50%, #418DE8 100%);
  width: 0;
  height: 2px;
  transition: width 250ms;
